<template>
  <div class="container">
    <mt-header title="帮助与反馈">
        <router-link to="/help" slot="left">
            <mt-button icon="back"></mt-button>
        </router-link>
    </mt-header>

    <section class="banner_middle">
      问题类型
    </section>
    
    <mt-cell title="积分问题"  is-link to="/helpIntegral">
        <img slot="icon" src="../../../assets/img/jifen.png" width="24" height="24">
    </mt-cell>
    <mt-cell title="直播问题"  is-link to="/helpLive">
        <img slot="icon" src="../../../assets/img/sanjiao.png" width="24" height="24">
    </mt-cell>
    <mt-cell title="账号问题"  is-link to="/helpAccount">
        <img slot="icon" src="../../../assets/img/geren.png" width="24" height="24">
    </mt-cell>
    <mt-cell title="其他"  is-link to="/helpOther">
        <img slot="icon" src="../../../assets/img/wenhao.png" width="24" height="24">
    </mt-cell>

    <div class="feedback"><span @click="feedback">意见反馈</span></div>
  </div>
</template>
<script>
export default {
  methods: {
    feedback() {
      this.$router.push({
        path: "/feedback"
      })
    }
  }
};
</script>
<style lang="less" scoped>
.banner_middle {
  margin: 20px auto;
  color: #777;
  font-size: 30px;
  padding-left: 30px;
}
.feedback {
  width: 100%;
  height: 80px;
  text-align: center;
  span {
    display: inline-block;
    height: 80px;
    line-height: 80px;
    color: #44b336;
    font-size: 30px;
  }
}
</style>



